<template>
  <div id="dataCenter">
    <div id="content">
      <div class="dataDiv">
        <ul style="width: calc(100% - 66px)">
          <li v-for="(tab,index) in tabs" :key="index">
            <div>
              <img :src="tab.src" alt="">
              <div>
                <p>{{tab.text}}</p>
                <p><strong>{{numToThousands(tab.num)}}</strong>{{tab.campany}}</p>
              </div>
            </div>
          </li>
        </ul>
        <div style="float: right;width: 55px;margin-top: -80px">
          <Button @click="downFile" style="height: 30px">导出</Button>
          <Button @click="tuisong" style="height: 30px;">推送</Button>
        </div>
      </div>
      <div class="statistical">
        <transition name="fade">
          <div class="sta_box" :class="{activeBox: boxstate === 1}" style="left:0;top:0;padding: 15px 5px 5px 30px">
            <div class="con_box">
              <i class="stateTitle stateTitle1"></i>
              <Form :model="areaData" :label-width="90" v-if="boxstate === 1" class="searchesBox">
                <FormItem prop="province" label="省：">
                  <Input v-model.trim="areaData.province" disabled/>
                </FormItem>
                <FormItem prop="city" label="市：">
                  <i-select v-model.trim="areaData.city" style="width: 100%" placeholder="请选择市" @on-change="searchState1">
                    <i-option v-for="item in cityList" :value="item.value">{{ item.label }}</i-option>
                  </i-select>
                </FormItem>
              </Form>
              <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 : 1" size="small" class="boxBtn"></Button>
              <div class="bigBox" :class="{smallToBig1:boxstate === 1}">
                <div class="smallBox">
                  <BarChart ref="BarChart" :bar1Data="bar1Data"></BarChart>
                </div>
                <div class="smallBox2">
                  <PieChart2 ref="PieChart2" :bar1Data="bar1Data"></PieChart2>
                </div>
                <div class="smallBox2">
                  <Table1 :bar1Data="bar1Data"></Table1>
                </div>
              </div>
            </div>
          </div>
        </transition>
        <div class="sta_box" :class="{activeBox: boxstate === 2}" style="left:50%;top:0;padding:15px 30px 5px 5px">
          <div class="con_box">
            <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="mapOpen" size="small" class="boxBtn"></Button>
            <Map :boxstate="boxstate" ref="maps" @layerConClick="layerConClick"></Map>
          </div>
        </div>
        <div class="sta_box" :class="{activeBox: boxstate === 3}" style="left:0;top:50%;padding: 5px 5px 15px 30px">
          <div class="con_box">
            <i class="stateTitle stateTitle3"></i>
            <Button :icon="boxstate === 3 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 3 ? 0 :3" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 3}">
              <div class="smallBox">
                <BarChart2 ref="BarChart2"></BarChart2>
              </div>
            </div>
          </div>
        </div>
        <div class="sta_box" :class="{activeBox: boxstate === 4}" style="left:50%;top:50%;padding: 5px 30px 15px 5px">
          <div class="con_box">
            <i class="stateTitle stateTitle4"></i>
            <Button :icon="boxstate === 4 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 4 ? 0 :4" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 4}">
              <div class="smallBox">
                <LineChart ref="LineChart"></LineChart>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="footer">
      <p>
        <span>Copyright © 安徽省生态环境厅  版权所有</span>
        <span style="margin-left: 80px">技术支持单位：生态环境部卫星环境应用中心</span>
      </p>
    </div>
    <Modal
      v-model="modal1"
      title="推送消息时间">
      <Form :model="tuisongdata"  :label-width="120">
        <FormItem label="发布时间" prop="time">
          <DatePicker type="date" v-model="tuisongdata.time" placeholder="Select date"></DatePicker>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="tuisong">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { toThousands } from '@/utils/common'
import { getTotal, getAreas, redlineinfoExport } from '@/api/dataCenter'
import { getMenu } from '@/api/acquisitionTable'
import { msgsetList, st4ScsCbdAdd } from '@/api/systemManage'
import moment from 'moment'

import Map from './OnePiece'
import LineChart from './LineChart.vue'
import BarChart from './BarChart.vue'
import BarChart2 from './BarChart2.vue'
import PieChart2 from './PieChart2.vue'
import Table1 from './Table1.vue'

import redThread from '../../assets/main_images/redThread.png'
import redThread1 from '../../assets/main_images/redThread1.png'
import redThread2 from '../../assets/main_images/redThread2.png'
import redThread3 from '../../assets/main_images/redThread3.png'
export default {
  name: 'DataCenter',
  data () {
    return {
      /* 获取统计数据 */
      totalData: {},
      tabs: [
        {
          text: '生态保护红线斑块数量',
          src: redThread,
          num: 0,
          campany: '个'
        },
        {
          text: '生态保护红线拐点数量',
          src: redThread1,
          num: 0,
          campany: '个'
        },
        {
          text: '生态保护红线界碑界桩数量',
          src: redThread2,
          num: 0,
          campany: '个'
        },
        {
          text: '生态保护红线标识牌数量',
          src: redThread3,
          num: 0,
          campany: '个'
        }
      ],
      /* 放大块的标识 */
      boxstate: 0,
      /* 行政区划数据 */
      clearCascader: false,
      areaData: {
        province: '安徽省',
        city: ''
      },
      cityList: [],
      /* 柱状图一数据 */
      bar1Data: [],
      msgset: {}, // 存储发布配置
      tuisongdata: {
        time: new Date()
      },
      modal1: false // 弹框控制
    }
  },
  mounted () {
    this.getTotal()
    // 获取行政区化数据
    this.getCodeMenu()
    this.getAreas()
    this.msgsetList()
  },
  components: {
    Map,
    LineChart,
    BarChart,
    BarChart2,
    PieChart2,
    Table1
  },
  methods: {
    /* 数据统计接口 */
    getTotal (e) {
      getTotal({
        data: {
          code: e || this.$store.getters.getloginInfor().comCode
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.totalData = res.data.data
          this.tabs[0].num = this.totalData.redlineCount
          this.tabs[1].num = this.totalData.pointCount
          this.tabs[2].num = this.totalData.markerCount
          this.tabs[3].num = this.totalData.boardCount
          this.tabs[4].num = this.totalData.surveyCount
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    /* 给数字每三位添加 ， */
    numToThousands (num) {
      return toThousands(num)
    },
    /* 地图框开关控制 */
    mapOpen () {
      this.boxstate = this.boxstate === 2 ? 0 : 2
      if (this.boxstate === 0) {
        this.$refs.maps.layerCon = false
      } else {
        this.$refs.maps.layerCon = true
      }
    },
    /* 点击图层控制器打开地图框 */
    layerConClick () {
      this.boxstate = 2
    },
    /* axios: 获取省市县菜单 */
    getCodeMenu () {
      getMenu({
        data: {
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.cityList = []
          res.data.data[0].children.forEach(element => {
            this.cityList.push({
              label: element.label,
              value: element.comCode
            })
          })
          this.cityList.unshift({
            label: '全部',
            value: ''
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取红线台账数据 */
    getAreas (e) {
      getAreas({
        data: {
          code: e || this.$store.getters.getloginInfor().comCode
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.bar1Data = []
          for (let item of res.data.data) {
            this.bar1Data.push(item)
          }
        }
      })
    },
    /* 导出报告 */
    downFile () {
      redlineinfoExport({
        data: {
          data1: this.$refs.BarChart.myChart.getDataURL({
            type: 'jpeg',
            pixelRatio: 1,
            backgroundColor: '#fff'
          }).substring(23),
          data2: this.$refs.BarChart2.myChart.getDataURL({
            type: 'jpeg',
            pixelRatio: 1,
            backgroundColor: '#fff'
          }).substring(23),
          data3: this.$refs.LineChart.myChart.getDataURL({
            type: 'jpeg',
            pixelRatio: 1,
            backgroundColor: '#fff'
          }).substring(23)
        }
      }).then(res => {
        if (res.data.code === '0000') {
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data
          link.href = url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      })
    },
    /* 控制红线台账行政区划查询 */
    searchState1 (val) {
      if (val === '') {
        this.getAreas(this.$store.getters.getloginInfor().comCode)
      } else {
        this.getAreas(val)
      }
    },
    // 推送配置
    msgsetList () {
      msgsetList({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          this.msgset = res.data.data.rows[0]
        } else {
          this.$Notice.error({ title: '获取' })
        }
      })
    },
    // 点击推送消息
    clickFabu () {
      if (this.msgset.time) {
        this.modal1 = true
      } else {
        this.tuisong()
      }
    },
    // 推送信息
    tuisong () {
      this.modal1 = false
      st4ScsCbdAdd({
        data: {
          time: moment(this.tuisongdata.time).format('YYYY-MM-DD'),
          user: '系统用户',
          midContent: '发布了新的红线统计分析报告',
          afterContent: '发布了新的红线统计分析报告',
          users: this.msgset.user,
          verify: this.msgset.verify ? 1 : 3
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '新建消息提醒成功' })
          this.tuisongdata = {
            time: new Date()
          }
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  #dataCenter{
    width:100%;
    min-width: 1200px;
    min-height:100%;
    background: #efeeee;
    background-size: 100% 100%;
    display:flex;
    flex-direction:column;
    padding-bottom: 10px;
    #content{
      text-align: center;
      height:calc(100vh - 120px);
      .dataDiv{
        padding: 0 30px;
        border-bottom: 1px solid #b7b6b6;
        border-top: 1px solid #545454;
        margin-top: 10px;
        ul{
          width: 100%;
          display: flex;
          margin:2px 0;
          li{
            flex:1;
            display: flex;
            padding:10px;
            border-left: 1px solid #c2c1c1;
            &:last-child{
              border-right: 1px solid #c2c1c1;
            }
            >div{
              width:100%;
              background: #fff;
              border-radius: 5px;
              >img{
                float: left;
                padding: 11px;
                width:70px;
                margin-left: 15px;
              }
              >div{
                padding:10px;
                p{
                  margin-top: 3px;
                  strong{
                    font-size: 20px;
                    font-weight: bold;
                    margin-right: 10px;
                  }
                }
              }
            }
          }
        }
      }
      .statistical{
        position: relative;
        width: 100%;
        height:calc(100% - 100px);
        padding:0 30px;
        margin: 0;
        .sta_box{
          position: absolute;
          height: 50%;
          width: 50%;
          padding:15px 20px;
          .con_box{
            height: 100%;
            width: 100%;
            background: #fff;
            border-radius: 10px;
            box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
            position: relative;
            overflow: hidden;
            .stateTitle {
              display: inline-block;
              position: absolute;
              top: 15px;
              left: 14px;
              width: 22px;
              height: 22px;
              &.stateTitle1 {
                background: url("../../assets/redLine/sprite.png") 0 0 no-repeat;
                background-size: cover;
              }
              &.stateTitle3 {
                width: 24px;
                height: 24px;
                background: url("../../assets/redLine/sprite.png") 0 -32px no-repeat;
                background-size: cover;
              }
              &.stateTitle4 {
                width: 24px;
                height: 24px;
                background: url("../../assets/redLine/sprite.png") 0 -63px no-repeat;
                background-size: cover;
              }
            }
            .boxBtn{
              position: absolute;
              right: 20px;
              top:10px;
              z-index: 80;
            }
            .bigBox{
              width:calc(100vw - 60px);
              height:100%;
              .smallBox{
                width:50%;
                height:100%;
                float: left;
             }
            }
            .smallToBig {
              .smallBox {
                width: 100%;
              }
            }
            .smallToBig1 {
              .smallBox {
                width: 100%;
                height: 50%
              }
              .smallBox2 {
                width: 50%;
                height: 50%;
                float: right;
              }
            }
          }
        }
        /* box放大 */
        .activeBox{
          left:0!important;
          top:0!important;
          padding: 15px 30px!important;
          width: 100%;
          height: 100%;
          z-index: 99;
          transition: all 0.8s ease;
        }
      }
      .searchesBox {
        position: absolute;
        top: 10px;
        left: 150px;
        z-index: 999;
        overflow: visible;
        .ivu-form-item {
          margin: 0;
          float: left;
          .ivu-select, ivu-input-wrapper {
            width: 150px !important;
          }
        }
      }
    }

    /*底部内容的样式*/
    #footer{
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      height: 27px;
      background:#fff;
      line-height: 27px;
      box-shadow: 0 0 15px 5px rgba(100,100,100,0.05);
      p{
        text-align: center;
        font-size: 12px;
        color:#999;
      }
    }
  }
</style>
